import { Component, ReactNode } from "react";
import style from "./Smtr.module.css";
import { RouteComponentProps } from "react-router-dom";
import { SmtrItemInfo } from "./SmtrContainer";
import Localization from "../../utils/localization/localization";
import Grid from "../common/grid/Grid";
import { GridItemSettings, GridSettings, InjectedControl, ValueType } from "../common/grid/GridSettings";
import { buildGridItemSettings, buildGridSettings } from "../../utils/helpers/settings-helper";
import { HiListBullet } from "react-icons/hi2";

type Props = {
    getSmtr: (id: number) => void,
    items: Array<SmtrItemInfo>
}

type RouteParams = {
    id: string
}

class Smtr extends Component<Props & RouteComponentProps<RouteParams>> {   
    state: Readonly<{projectId: number, isStructureVisible: boolean}> = {
        projectId: Number(this.props.match.params.id),
        isStructureVisible: false
    }

    localization = Localization.getLocalization("smtr");

    getSmtr = (skip: number, take: number) => {
        this.props.getSmtr(this.state.projectId);
    }

    onStructureToggle = () => {
        this.setState({isStructureVisible: !this.state.isStructureVisible});
    }

    getSmtrSettings = (): GridSettings => {
        const injectedControls: Array<InjectedControl> = [
            {
                title: "/  ",
                iconComponent: HiListBullet,
                onClick: this.onStructureToggle
            },
        ];

        const itemSettings = new Map<string, GridItemSettings>();
        itemSettings.set("index", buildGridItemSettings(" /", 60, ValueType.Number, false));
        itemSettings.set("stage", buildGridItemSettings("", 70, ValueType.String));
        itemSettings.set("title", buildGridItemSettings("", 70, ValueType.Number));
        itemSettings.set("titleName", buildGridItemSettings(" ", 300, ValueType.String));
        itemSettings.set("brand", buildGridItemSettings(" / ", 70, ValueType.String));
        itemSettings.set("techSpecCode", buildGridItemSettings(" . ", 170, ValueType.String));
        itemSettings.set("revision", buildGridItemSettings(". . .", 80, ValueType.Number));
        itemSettings.set("constructive", buildGridItemSettings("", 200, ValueType.String));
        itemSettings.set("position", buildGridItemSettings("", 80, ValueType.Number));
        itemSettings.set("techName", buildGridItemSettings("   ", 450, ValueType.String, true, true));
        itemSettings.set("docType", buildGridItemSettings(", ", 120, ValueType.String));
        itemSettings.set("code", buildGridItemSettings(" ", 120, ValueType.String));
        itemSettings.set("manufacturer", buildGridItemSettings("", 100, ValueType.String));
        itemSettings.set("units", buildGridItemSettings(". .", 70, ValueType.String));
        itemSettings.set("quantityInProject", buildGridItemSettings("-  ", 90, ValueType.Money));
        itemSettings.set("mass", buildGridItemSettings(" 1 ., ", 80, ValueType.Number));
        itemSettings.set("note", buildGridItemSettings("", 200, ValueType.String));
        itemSettings.set("consumptionRate", buildGridItemSettings(" ", 100, ValueType.Persents));
        itemSettings.set("consumptionQuantity", buildGridItemSettings("-    ", 150, ValueType.Money, false));
        itemSettings.set("resourceType", buildGridItemSettings("", 150, ValueType.String));
        itemSettings.set("delivery", buildGridItemSettings(" ", 120, ValueType.String));
        // itemSettings.set("pricePerUnit", buildGridItemSettings("", 110, ValueType.Number));
        // itemSettings.set("cost", buildGridItemSettings("", 110, ValueType.Number));
        itemSettings.set("workCode", buildGridItemSettings(" ", 150, ValueType.String));
        itemSettings.set("workName", buildGridItemSettings(" ", 600, ValueType.String, true, true));
        itemSettings.set("workUnits", buildGridItemSettings(". .", 70, ValueType.String));
        itemSettings.set("coeficient", buildGridItemSettings("K", 60, ValueType.Money));
        itemSettings.set("quantity", buildGridItemSettings("", 110, ValueType.Money, false));

        return buildGridSettings(itemSettings, injectedControls);
    }

    render(): ReactNode {
        return (
            <div className={style.container}>
                {
                    this.state.isStructureVisible && 
                        <div className={style.structureContainer}>
                            <p><strong> </strong></p>
                        </div>
                }
                <div className={style.gridContainer}>
                    <Grid
                        items={this.props.items}
                        settings={this.getSmtrSettings()}
                        getItems={this.getSmtr}
                        createItem={() => {}}
                        editItem={() => {}}
                        deleteItem={() => {}}
                    />
                </div>
            </div>
        );
    }
}

export default Smtr;